<template>
  <div class="container">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>订单</el-breadcrumb-item>
      <el-breadcrumb-item>订单审核</el-breadcrumb-item>
      <el-breadcrumb-item>订单审核</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="rowContainer">
      <el-cascader
        placeholder="试试搜索：马来待确认"
        :options="options"
        :value="value1"
        filterable
        change-on-select
        @change="assignCompleter"
        clearable
      ></el-cascader>
    </div>
    <div class="tableContainer">
      <el-table
        :data="tableData"
        height="100%"
        border
        style="width: 100%" class="table">
        <el-table-column
          prop="order_id"
          label="订单号">
          <template slot-scope="scope">
           <el-button type="text" @click="openDetail(scope.row.order_id)">{{ scope.row.order_id }}</el-button>
          </template>
        </el-table-column>
        <el-table-column
          prop="price"
          label="金额">
        </el-table-column>
        <el-table-column
          prop="website"
          label="站点">
        </el-table-column>
        <el-table-column
          prop="country"
          label="地区">
        </el-table-column>
        <el-table-column
          prop="name"
          label="下单人">
        </el-table-column>
        <el-table-column
          prop="phone"
          label="电话">
        </el-table-column>
        <el-table-column
          prop="ip"
          label="ip">
        </el-table-column>
        <el-table-column
          prop="comment"
          label="客户备注">
        </el-table-column>
        <el-table-column
          prop="create_time"
          label="下单时间">
        </el-table-column>
        <el-table-column
          width="200"
          prop="status"
          label="状态">
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
              <el-button
              size="mini"
              type="success"
              @click="handleClk(scope.row.order_id,2)">确认订单</el-button>
              <el-button
              size="mini"
              type="danger"
              @click="handleClk(scope.row.order_id,10)">取消订单</el-button>
              <el-button
              size="mini"
              type="primary"
              @click="handleClk(scope.row.order_id,'update')">修改订单</el-button>
              <el-button
              size="mini"
              type="danger"
              @click="setProblemOrder(scope.row)">设为待处理订单</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalSize">
      </el-pagination>

      <el-dialog title="标注问题单" :visible.sync="dialogFormVisible">
        <el-form :model="form" :rules="rules" ref="form">
          <el-form-item label="物流单号" :label-width="formLabelWidth" prop="track_number">
            <el-input v-model="form.track_number" type="text" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="问题类型" :label-width="formLabelWidth" prop="type">
            <el-select v-model="form.type" placeholder="请选择">
              <el-option label="其他" value="0"></el-option>
              <el-option label="地址有误" value="1"></el-option>
              <el-option label="电话有误" value="2"></el-option>
              <el-option label="电话无人接听" value="3"></el-option>
              <el-option label="签收失败" value="4"></el-option>
              <el-option label="无人收货" value="5"></el-option>
              <el-option label="客户无法现金支付" value="6"></el-option>
              <el-option label="客户要求取消订单" value="7"></el-option>
              <el-option label="客户希望换一天送货" value="8"></el-option>
              <el-option label="客户拒绝付款" value="9"></el-option>
              <el-option label="电话地址都有误" value="10"></el-option>
              <el-option label="客户说未曾订购" value="11"></el-option>
              <el-option label="颜色不详" value="12"></el-option>
              <el-option label="尺寸不详" value="13"></el-option>
              <el-option label="件数不详" value="14"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="问题状态" :label-width="formLabelWidth" prop="status">
            <el-select v-model="form.status" placeholder="请选择">
              <el-option label="处理中" value="0"></el-option>
              <el-option label="重新派送" value="1"></el-option>
              <el-option label="取消订单" value="2"></el-option>
              <el-option label="拒签" value="3"></el-option>
              <el-option label="签收" value="4"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="备注" :label-width="formLabelWidth" prop="remark">
            <el-input v-model="form.remark" type="textarea" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="pSubmit('form',currentRow.order_id)">提 交</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
        return {
          options: [{
            value: '1',
            label: '待确认订单',
            children: [{
              label: '马来西亚',
              value: 'MY',
            },{
              label: '新加坡',
              value: 'SG',
            },{
              label: '泰国',
              value: 'TH',
            },{
              label: '印尼',
              value: 'ID',
            },{
              label: '台湾',
              value: 'TW',
            },{
              label: '香港',
              value: 'HK',
            },{
              label: '阿联酋',
              value: 'UA',
            }]
          },{
            value: '20',
            label: '待处理订单',
            children: [{
              label: '马来西亚',
              value: 'MY',
            },{
              label: '新加坡',
              value: 'SG',
            },{
              label: '泰国',
              value: 'TH',
            },{
              label: '印尼',
              value: 'ID',
            },{
              label: '台湾',
              value: 'TW',
            },{
              label: '香港',
              value: 'HK',
            },{
              label: '阿联酋',
              value: 'UA',
            }]
          }],
          dialogFormVisible: false,
          form: {
            remark: '',
            type: [],
            track_number: '',
            status: [],
          },
          rules: {
            track_number: [
              { required: true, message: '请输入物流单号', trigger: 'blur' }
            ],
            type: [
              { required: true, message: '请选择问题类型', trigger: 'change' }
            ],
            status: [
              { required: true, required: true, message: '请选择问题状态', trigger: 'change' }
            ],
            remark: [
              { required: true, required: true, message: '请输入备注', trigger: 'blur' }
            ],

          },
          formLabelWidth: '120px',
          value1: ['',''],
          tableData: [],
          totalSize: 0,
          currentPage: 1,
          pageSize: 10,
          currentRow: null
        }
    },
    methods: {
      setProblemOrder(row){
        this.dialogFormVisible = true;
        this.currentRow = row;
      },
      handleSizeChange(val) {
        this.pageSize = val;
        console.log(val);
        this.initTable(this.value1[0],this.value1[1]);
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        console.log(val);
        this.initTable(this.value1[0],this.value1[1]);
      },
      assignCompleter(select) {
        if(!select[0] || select[0]=='undifind'){
          select[0] = '';
        }
        if(!select[1] || select[1]=='undifind'){
          select[1] = '';
        }
        this.value1 = select;
        this.initTable(select[0],select[1]);
      },
      openDetail(order_id) {
        this.$router.push('/order/orderDetail?id='+order_id);
      },
      handleClk(order_id,status) {
        if(status=='update'){
          let id = {id: order_id};
          this.$router.push({path: '/order/updateOrder',query: id});
        }else{
          this.$confirm('是否继续此操作?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            var that = this;
            this.$axios.post('/v1/order/update-status',{'status':status,'order_id':order_id}).then((re) => {
              console.log(re);
              this.dialogFormVisible = false;
              if(re.data.error != ''){
                that.$message({
                  type: 'info',
                  message: re.data.error
                });
              }else{
                that.$message({
                  type: 'success',
                  message: '成功!'
                });
              }
            }).catch((re) => {
              console.log(re);
            });

          }).catch((res) => {
            console.log(res);
          });
        }
      },
      pSubmit(formName,order_id){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let that = this;
            this.$axios.post('/v1/order/update-status',{'status':20,'order_id':order_id,'type':this.form.type,'remark':this.form.remark,'track_number':this.form.track_number,'sonStatus':this.form.status}).then((re) => {
              console.log(re);
              this.dialogFormVisible = false;
              if(re.data.error != ''){
                that.$message({
                  type: 'info',
                  message: re.data.error
                });
              }else{
                that.$message({
                  type: 'success',
                  message: '成功!'
                });
              }
            }).catch((re) => {
                console.log(re);
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });

      },
      initTable(st,co) {
        let url = '/v1/order/audit-order?page='+this.currentPage+'&pageSize='+this.pageSize+'&status='+st+'&country='+co;
        let that = this;
        let initList = [];
        this.$axios.get(url).then((res) => {
          if(res.data.code == 200){
            console.log(res.data.data);
            let data = res.data.data.list;
            that.totalSize = parseInt(res.data.data.totalSize);
            data.forEach((re) => {
              let order = {};
              order.order_id = re.id;
              order.price = re.total_price;
              order.website = re.website;
              order.country = re.country;
              order.name = re.user_name;
              order.phone = re.phone;
              order.ip = re.user_ip;
              order.comment = re.comment;
              order.create_time = re.create_time;
              order.status = re.status;
              initList.push(order);
            })
            that.tableData = initList;
          }else{
            that.$message({
              type: 'error',
              message: '搜索失败!'
            });
          }
        }).catch((err) => {
          that.$message({
            type: 'error',
            message: '网络不好，请重试!'
          });
        })
      }
    },
    created() {
      this.initTable('','');
    }
  }
</script>

<style scoped lang="stylus">
  .container
    padding 30px
    position relative
    .rowContainer
      margin:20px 0px
    .tableContainer
      position absolute
      left 30px
      right: 30px
      top: 120px
      bottom: 0
      display flex
      flex-direction column
      .table
        flex 1
        .cell
          text-align center
          button
            margin: 2px 0
      .el-pagination
        text-align right
        margin 8px 0
</style>



